@import '../variables.sass';

.navbar-search {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-left: 150px;
  background-color: $light_blue;
  align-items: center;
  height: 100px;
  a {
    img {
      height: 100px; }
    text-decoration: none; }
  a:hover {
    text-decoration: none; } }

.search-bar-wrapper {
  display: flex;
  height: 80px;
  form {
    display: flex;
    width: 50vw;
    justify-content: space-between;
    align-items: center; }
  input {
    background-color: #FFF !important;
    border-radius: 20px 0 0 20px;
    height: 50px; }
  input:focus {
    outline-width: 0; }

  .search-bar {
    display: flex;
    width: 80%; }

  .search-bar-btn {
    display: flex;
    justify-self: center;
    align-items: center;
    background-color: white;
    margin: 5px 0 11px 0;
    border: none;
    border-radius: 0 20px 20px 0;
    padding: 10px;
    height: 50px; } }
.navbar-btn {
  border: none;
  background-color: $blue;
  padding: 20px;
  margin: 5px 0 11px 0;
  border-radius: 10px;
  height: 50px;
  width: 130px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  margin-left: 10px; }

.navbar-right {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  * {
    padding-right: 20px; } }

.filter {
  position: relative;
  background-color: $blue;
  max-width: 200px;
  border-radius: 10px;
  padding-left: 10px;
  margin-left: 10px;
  color: Black;
  text: bold;
  font-size: 18px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer; }

.message-button {
  border: none;
  background-color: $blue;
  padding: 20px;
  margin: 5px 0 11px 0;
  border-radius: 10px;
  height: 50px;
  width: 130px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  margin-left: 10px; }
